// special finsemble component to display one row of table data in JSON format; incoked by LoggerMainConsoleTable component

const JSONFormatter = require("json-formatter-js").default;
console.log("JSONFormatter start");

function ready() {

	fin.desktop.Window.getCurrent().getOptions(function (opts) {
		var logData = opts.customData.spawnData.logData;
		var index = opts.customData.spawnData.index;
		var viewDepth = opts.customData.spawnData.viewDepth;
		console.log("JSONFormatter", logData, index, viewDepth);
		let div = document.createElement("div");
		div.innerHTML = "<h3>" + "Central Log Data: Line " + (index + 1) + "</h3><hr>";
		document.body.appendChild(div);

		if (viewDepth < 1) {
			viewDepth = 1;
		}
		let dataFormat = new JSONFormatter({
			Type: logData.logType,
			ClientName: logData.logClientName,
			TimeStamp: logData.logTimestamp,
			ConsoleArguments: logData.parsedLogArgs
		}, viewDepth, {
			hoverPreviewEnabled: true,
			hoverPreviewArrayCount: 100,
			hoverPreviewFieldCount: 5,
			theme: "",
			animateOpen: true,
			animateClose: true
		  });
		document.body.appendChild(dataFormat.render());
	});
}

window.addEventListener("DOMContentLoaded", function () {
	console.log("In DOMContentLoaded");
	FSBL.addEventListener("onReady", function () {
		console.log("JSONFormatter onReady");
		ready();
	});
});




